import { ERROR_CODE, BaseError } from './errors';

async function getData(url, signal) {
    let response;
    try {
        response = await fetch(url, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            },
            redirect: 'follow',
            signal,
        });
    } catch (error) {
        if (error instanceof DOMException) {
            return [null, error];
        }
        return [null, new BaseError(ERROR_CODE.NetworkError, error.message)];
    }

    if (response.ok) {
        return [await response.json(), null];
    }
    if (response.status === 400) {
        return [null, new BaseError(ERROR_CODE.BadRequest, '–ù–µ–∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π –∑–∞–ø—Ä–æ—Å.')];
    }
    if (response.status === 404) {
        return [null, new BaseError(ERROR_CODE.NotFound, '–ò–Ω—Ñ–æ—Ä–º–∞—Ü–∏—è –Ω–µ –Ω–∞–π–¥–µ–Ω–∞.')];
    }
    return [null, new BaseError(ERROR_CODE.UnexpectedError, '–ß—Ç–æ-—Ç–æ –ø–æ—à–ª–æ –Ω–µ —Ç–∞–∫.')];
}

export { getData };
